/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react'
import '../header/header.css'

class Header extends Component {
  state = {
    menu: 'closed',
  }
  menuOnClickHandler = () => {
    const menu = document.querySelector('.header__menu')
    if (this.state.menu === 'closed') {
      menu.style.height = '150px'
      this.setState({ menu: 'opened' })
    } else {
      menu.style.height = '0px'
      this.setState({ menu: 'closed' })
    }
  }
  menuItemsOnClickHandler = e => {
    return e.target.className.slice(0, 18) === 'header__menu_item '
      ? this.menuOnClickHandler()
      : null
  }

  render() {
    return (
      <header>
        <div className="header__logo" name="home"></div>
        <div className="header__menu-global">
          <a
            href="#home"
            className="header__menu-global_item header__menu-global_home">
            home.
          </a>
          <a
            href="#about-us"
            className="header__menu-global_item header__menu-global_about-us">
            about us.
          </a>
          <a
            href="#portfolio"
            className="header__menu-global_item header__menu-global_portfolio">
            portfolio.
          </a>
          <a
            href="#portfolio"
            className="header__menu-global_item header__menu-global_blog">
            blog.
          </a>
          <a
            href="#contact-us"
            className="header__menu-global_item header__menu-global_contact-us">
            contact us.
          </a>
        </div>
        <select className="header__language">
          <option>En</option>
        </select>
        <div className="header__contuct-us">contact us. ⟶</div>

        <div className="header__hamburger" onClick={this.menuOnClickHandler}>
          <div className="header__hamburger_stick"></div>
          <div className="header__hamburger_stick"></div>
          <div className="header__hamburger_stick"></div>
        </div>
        <div className="header__menu" onClick={this.menuItemsOnClickHandler}>
          <a href="#home" className="header__menu_item header__menu_home">
            home.
          </a>
          <a
            href="#about-us"
            className="header__menu_item header__menu_about-us">
            about us.
          </a>
          <a
            href="#portfolio"
            className="header__menu_item header__menu_portfolio">
            portfolio.
          </a>
          <a href="#portfolio" className="header__menu_item header__menu_blog">
            blog.
          </a>
          <a
            href="#contact-us"
            className="header__menu_item header__menu_contact-us">
            contact us.
          </a>
        </div>
      </header>
    )
  }
}
export default Header
