/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react'
import Slider from './slider/slider'
import '../main/main.css'

class Main extends Component {
  render() {
    return (
      <main>
        <Slider>
          {' '}
          <a name="about-us" />
        </Slider>
        <div className="main__portfolio">
          <a name="portfolio" />
          <div className="main__portfolio_header">
            See some of our
            <br />
            Creative work.
          </div>
          <div className="main__portfolio_content-wrapper">
            <div className="main__portfolio_content"></div>
            <div className="main__portfolio_content"></div>
            <div className="main__portfolio_content"></div>
            <div className="main__portfolio_content"></div>
            <div className="main__portfolio_content"></div>
            <div className="main__portfolio_content"></div>
          </div>
          <div className="main__portfolio_more-info">See More of These.</div>
        </div>
        <div className="main__contacts">
          <a name="contact-us" />
          <div className="main__contacts_link">CONTACT US</div>
          <div className="main__contacts_header">Get in Touch</div>
          <div className="main__contacts_data">
            <input
              type="text"
              value="Your email"
              className="data_email"></input>
            <input type="text" value="Subject" className="data_subject"></input>
          </div>
          <input
            type="text"
            value="Message here..."
            className="main__contacts_message"></input>
          <div className="main__contacts_send">Send Message</div>
        </div>
      </main>
    )
  }
}
export default Main
