import React, { Component } from 'react'
import '../slider/slider.css'

class Slider extends Component {
  state = {
    content: 0,
  }
  leftOnClickHandler = e => {
    const cases = Array.from(document.querySelectorAll('.slider-content_case'))
    cases.forEach(e => {
      e.style.opacity = 0
    })

    this.state.content > 0
      ? this.setState(
          {
            content: this.state.content - 1,
          },
          () => {
            cases[this.state.content].style.opacity = 1
          }
        )
      : this.setState(
          {
            content: 2,
          },
          () => {
            cases[this.state.content].style.opacity = 1
          }
        )
  }
  rightOnClickHandler = e => {
    const cases = Array.from(document.querySelectorAll('.slider-content_case'))
    cases.forEach(e => {
      e.style.opacity = 0
    })

    this.state.content < 2
      ? this.setState(
          {
            content: this.state.content + 1,
          },
          () => {
            cases[this.state.content].style.opacity = 1
          }
        )
      : this.setState(
          {
            content: 0,
          },
          () => {
            cases[this.state.content].style.opacity = 1
          }
        )
  }
  render() {
    return (
      <div className="main__slider">
        <div
          className="main__slider_left"
          onClick={this.leftOnClickHandler}></div>
        <div className="main__slider_content">
          <div className="slider-content_case slider-content_case0">
            <div className="slider-content_text">
              <div className="text_header">The Spirit of Digital Agency.</div>
              <div className="text_description">
                Vestibulum ac diam sit amet quam vehicula elementum amet est on
                dui. Nulla porttitor accumsan tincidunt.
              </div>
              <div className="text_additional-info">
                <div className="text_additional-info_more-about">
                  more about us
                </div>
                <div className="text_additional-info_get-in-touch">
                  get in touch.
                </div>
              </div>
            </div>
            <div className="slider-content_image"></div>
          </div>
          <div className="slider-content_case slider-content_case1">
            <div className="slider-content_text">
              <div className="text_header">Lorem ipsum.</div>
              <div className="text_description">
                Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                Impedit enim non unde dignissimos beatae numquam, totam
                explicabo! Delectus culpa, aperiam nobis harum amet repudiandae
                aspernatur aliquam nesciunt enim laborum omnis neque doloribus
                quos ipsam doloremque inventore optio porro, blanditiis nam!
              </div>
              <div className="text_additional-info">
                <div className="text_additional-info_more-about">
                  more about us
                </div>
                <div className="text_additional-info_get-in-touch">
                  get in touch.
                </div>
              </div>
            </div>
            <div className="slider-content_image"></div>
          </div>
          <div className="slider-content_case slider-content_case2">
            <div className="slider-content_text">
              <div className="text_header">Lorem, ipsum dolor.</div>
              <div className="text_description">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Debitis
                facere sint minima, perspiciatis consequatur distinctio autem
                aperiam recusandae ullam eaque!
              </div>
              <div className="text_additional-info">
                <div className="text_additional-info_more-about">
                  more about us
                </div>
                <div className="text_additional-info_get-in-touch">
                  get in touch.
                </div>
              </div>
            </div>
            <div className="slider-content_image"></div>
          </div>
        </div>
        <div className="main__slider_right" onClick={this.rightOnClickHandler}>
          {' '}
        </div>
      </div>
    )
  }
}
export default Slider
